.hero {
    padding-top: 220px;
    padding-bottom: 354px;
    background-image: url("../../img/hero-bg.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: var(--text-color);

    &__content {
        max-width: 590px;
    }

    &__btns {
        .btn {
            &:not(:last-child) {
                margin-right: 20px;
            }
        }
    }
}